<template>
  <div>
    <div v-for="u in urls" :key="u" class="text item" :title="u" @click="urlSelected(u)">
      <span class="urls-text">{{u}}</span>
    </div>
  </div>
</template>

<script>
  import store from 'store'
  export default {
    data () {
      return {
        urls: []
      }
    },
    mounted () {
      this.urls = store.get('urls') || []
      this.$emit('lengthChange', this.urls.length)
    },
    methods: {
      addHistory (url) {
        var urls = this.urls
        if (urls && urls.length > 0) {
          var index = urls.indexOf(url)
          if (index >= 0) {
            urls.splice(index, 1)
          }
        }
        urls.unshift(url)
        if (urls.length > 10) {
          urls.pop()
        }
        store.set('urls', urls)
        this.$emit('lengthChange', this.urls.length)
      },
      emptyHistory () {
        store.clearAll()
        this.urls = []
        this.$emit('lengthChange', 0)
      },
      urlSelected (url) {
        this.$emit('urlSelected', url)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .url-input, .url-history, .big-title {
    width: 80%;
    margin: 5px auto;
    text-align: center;
  }
  .item {
    padding: 8px 5px 5px 5px;
    border-bottom: 1px solid #cfd3de;
    word-break: break-all;
    overflow: hidden;
    height: 34px;
    width: 100%;
    cursor: pointer;
    text-overflow: ellipsis;
  }
  .item:hover {
    background-color: rgba(233, 237, 248, 0.52);
  }

  .item .urls-text {
    padding: 4px;
    white-space: nowrap;
  }
</style>



