<template>
  <div class="model">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span class="model-name">
          <i class="el-icon-star-on"></i>
          {{model.name}}
        </span>
        <!--<el-button style="float: right; padding: 3px 0" type="text">操作按钮</el-button>-->
      </div>

      <div>
        <el-table
          :data="model.fieldItems"
          style="width: 100%">
          <el-table-column prop="name" label="字段名称" width="180"></el-table-column>
          <el-table-column prop="type" label="字段类型" width="180"></el-table-column>
          <el-table-column prop="required" label="是否必填" width="180">
            <template slot-scope="scope">
              <i v-if="scope.row.required" class="el-icon-success"></i>
              <i v-else class="el-icon-error"></i>
            </template>
          </el-table-column>
          <el-table-column prop="description" label="描述"></el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'model',
  props: [
    'name',
    'modelItem'
  ],
  data () {
    return {
      model: {},
      tableData: [{
        date: '2016-05-02',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-04',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1517 弄'
      }, {
        date: '2016-05-01',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1519 弄'
      }, {
        date: '2016-05-03',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1516 弄'
      }]
    }
  },
  mounted () {
    this.reload(this.name)
  },
  watch: {
    name (newName) {
      this.reload(newName)
    }
  },
  methods: {
    reload (modelName) {
      if (this.modelItem) {
        this.model = this.modelItem
      } else {
        for (let m of this.$store.state.api.modelItems) {
          if (m.name === modelName) {
            this.model = m
            break
          }
        }
      }
    }
  }
}
</script>

<style scoped>
  .model-name {
    font-size: 20px;
    font-weight: bold;
  }
</style>



