<template>
  <div class="layout">
    <el-container class="all-container">

      <!--header-->
      <el-header class="header">
        <page-header></page-header>
      </el-header>

      <el-container class="main-container">
        <router-view/>
      </el-container>
    </el-container>
  </div>
</template>

<script>
  import pageHeader from '@/components/page_header'
  import leftNav from '@/components/left_nav'
  export default {
    name: 'layout',
    data () {
      return {
        showNavBar: false
      }
    },
    mounted () {
      if (this.$store.url) {
        this.showNavBar = true
      }
    },
    methods: {
    },
    components: {
      pageHeader,
      leftNav
    }
  }
</script>

<style scoped>
  .layout {
    height: 100%;
  }

  .all-container, .main-container, .left-nav, .inner-container {
    height: 100%;
  }

  .left-nav {
    width: 300px;
  }

  .header {
    padding: 0px;
  }

</style>



