import Api from '@/model/Api'
import TagItem from '@/model/path/TagItem'
import PathItem from '@/model/path/PathItem'
import ParameterItem from '@/model/path/ParameterItem'
import ResponseItem from '@/model/path/ResponseItem'
import InfoItem from '@/model/info/InfoItem'
import ContactItem from '@/model/info/ContactItem'
import LicenseItem from '@/model/info/LicenseItem'
import ModelItem from '@/model/model/ModelItem'
import FieldItem from '@/model/model/FieldItem'

var swaggerConvertor = {

  convert (swaggerApi) {
    var resultApi = new Api()
    resultApi.host = swaggerApi.host || window.location.host
    resultApi.basePath = swaggerApi.basePath
    resultApi.schemes = swaggerApi.schemes || [window.location.protocol.slice(0, -1)]

    resultApi.infoItem = convertInfo(swaggerApi)
    resultApi.tagItems = convertTagItems(swaggerApi, resultApi)
    resultApi.pathItems = convertPathItems(swaggerApi, resultApi)
    resultApi.modelItems = convertModelItems(swaggerApi)

    return resultApi
  }
}

function convertInfo (swaggerApi) {
  var info = swaggerApi.info
  var infoItem = new InfoItem()
  infoItem.title = info.title
  infoItem.description = info.description
  infoItem.version = info.version

  var contact = info.contact
  if (contact) {
    var contactItem = new ContactItem()
    contactItem.name = contact.name
    contactItem.url = contact.url
    contactItem.email = contact.email
    infoItem.contactItem = contactItem
  }

  var license = info.license
  if (license) {
    var licenseItem = new LicenseItem()
    licenseItem.name = license.name
    licenseItem.url = license.url
    infoItem.licenseItem = licenseItem
  }

  return infoItem
}

function convertPathItems (swaggerApi, resultApi) {
  var swaggerPaths = swaggerApi.paths
  if (!swaggerPaths || swaggerPaths.length === 0) {
    return []
  }

  var pathItems = []
  for (let path in swaggerPaths) {
    var methods = swaggerPaths[path]
    for (let method in methods) {
      var api = methods[method]

      var pathItem = new PathItem()
      pathItem.path = path
      pathItem.method = method
      pathItem.summary = api.summary
      pathItem.description = api.description
      pathItem.deprecated = api.deprecated
      pathItem.tags = api.tags
      pathItem.parameterItems = convertParameterItems(api.parameters)
      pathItem.responseItems = convertResponseItems(api.responses)
      pathItem.raw = JSON.stringify(api, null, 4)

      handleUndeclaredTags(pathItem.tags, resultApi)

      pathItems.push(pathItem)
    }
  }
  return pathItems
}

function handleUndeclaredTags (tags, resultApi) {
  if (!tags || tags.length === 0) {
    return
  }
  for (let tag of tags) {
    if (resultApi._tags[tag]) {
      continue
    }
    var tagItem = new TagItem()
    tagItem.name = tag
    resultApi.tagItems.push(tagItem)
    resultApi._tags[tag] = true
  }
}

function convertModelItems (swaggerApi) {
  var definitions = swaggerApi.definitions
  if (!definitions || Object.keys(definitions).length === 0) {
    return []
  }

  var modelItems = []
  for (let name in definitions) {
    var definition = definitions[name]
    var modelItem = new ModelItem()
    modelItem.name = name
    modelItem.description = definition.description
    modelItem.fieldItems = convertFieldItems(definition)

    modelItems.push(modelItem)
  }
  return modelItems
}

function convertFieldItems (definition) {
  var properties = definition.properties
  if (!properties || Object.keys(properties).length === 0) {
    return []
  }

  var fieldItems = []
  for (let name in properties) {
    let property = properties[name]
    let fieldItem = new FieldItem()
    fieldItem.name = name
    fieldItem.type = property.type || 'TODO 未解析'
    fieldItem.description = property.description
    fieldItem.required = false

    fieldItems.push(fieldItem)
  }

  // handle required
  if (definition.required && definition.required.length > 0) {
    for (let requiredFieldName of definition.required) {
      for (let fieldItem of fieldItems) {
        if (fieldItem.name === requiredFieldName) {
          fieldItem.required = true
          break
        }
      }
    }
  }

  return fieldItems
}

function convertTagItems (swaggerApi, resultApi) {
  var tags = swaggerApi.tags
  if (!tags || tags.length === 0) {
    return []
  }

  var tagItems = []
  var _tags = {}
  for (let tag of tags) {
    var tagItem = new TagItem()
    tagItem.name = tag.name
    tagItem.description = tag.description

    tagItems.push(tagItem)
    _tags[tag.name] = true
  }

  resultApi._tags = _tags
  return tagItems
}

function convertParameterItems (parameters) {
  if (!parameters || parameters.length === 0) {
    return []
  }

  var parameterItems = []
  for (let parameter of parameters) {
    var parameterItem = new ParameterItem()
    parameterItem.name = parameter.name
    parameterItem.in = parameter.in
    parameterItem.required = parameter.required || false
    parameterItem.description = parameter.description
    parameterItem.deprecated = parameter.deprecated
    parameterItem.dataType = resolveDataType(parameter)

    parameterItems.push(parameterItem)
  }
  return parameterItems
}

function convertResponseItems (responses) {
  var responseItems = []
  for (let code in responses) {
    var response = responses[code]
    var responseItem = new ResponseItem()
    responseItem.code = code
    responseItem.description = response.description
    responseItem.dataType = resolveDataType(response)

    responseItems.push(responseItem)
  }
  return responseItems
}

function resolveDataType (parameter) {
  // TODO
  return 'String'
}

export default swaggerConvertor



