import router from '@/router'
import localStore from 'store'
var store = require('@/store').default

router.beforeEach((to, from, next) => {
  if (to.name === 'url') {
    next()
    return
  }

  var showNav = to.query.showNav
  if (showNav) {
    if (showNav === 'false') {
      store.commit('showNav', false)
    }
  }

  // analysed
  if (store.state.api) {
    next()
    return
  }

  // new open with url
  var url = to.query.url
  if (!url) {
    url = localStore.get('rememberUrl')
  }
  if (url) {
    store.commit('initApi', {
      url,
      success: () => {
        next()
      },
      failed: (resp) => {
        next({name: 'url'})
      }
    })
    store.commit('setUrl', url)
    return
  }

  // new open without url
  next({name: 'url'})
})

export default {}



