import swagger2Convertor from './swagger2_convertor'
import swagger3Convertor from './swagger3_convertor'

let SWAGGER_V2 = 'swagger_v2'
let SWAGGER_V3 = 'swagger_v3'

var apiConvertor = {
  convert (doc) {
    var apiType = resolveApiType(doc)
    var result = null
    switch (apiType) {
      case SWAGGER_V2:
        result = swagger2Convertor.convert(doc)
        break
      case SWAGGER_V3:
        result = swagger3Convertor.convert(doc)
        break
      default:
        break
    }
    return result
  }
}

function resolveApiType (doc) {
  if (doc.swagger && parseInt(doc.swagger) === 2) {
    return SWAGGER_V2
  }
  if (doc.swagger && parseInt(doc.swagger) === 3) {
    return SWAGGER_V3
  }
  throw new Error('Unknown API document type')
}

export default apiConvertor



