<template>
  <div class="info">
    <el-card class="box-card">
      <div slot="header" class="clearfix info-header">
        <span class="info-title" v-text="info.title"></span>
        <el-tag type="info" size="mini" v-text="info.version"></el-tag>

        <Dropdown class="about-btn" v-if="info.termsOfService || info.licenseItem || info.contactItem">
          <a href="javascript:void(0)">
            关于此 API
            <Icon type="arrow-down-b"></Icon>
          </a>
          <DropdownMenu slot="list">
            <DropdownItem v-if="info.termsOfService">
              <a :href="info.termsOfService">服务条款</a>
            </DropdownItem>
            <Dropdown v-if="info.licenseItem" placement="left-start">
              <DropdownItem>
                许可协议
                <Icon type="ios-arrow-left"></Icon>
              </DropdownItem>
              <DropdownMenu slot="list">
                <DropdownItem>
                  <a :href="info.licenseItem.url">
                    <Icon type="android-document"></Icon> {{info.licenseItem.name}}
                  </a>
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
            <Dropdown v-if="info.contactItem" placement="left-start">
              <DropdownItem>
                联系信息
                <Icon type="ios-arrow-left"></Icon>
              </DropdownItem>
              <DropdownMenu slot="list">
                <DropdownItem v-if="info.contactItem.name">
                  <Icon type="person-stalker"></Icon> {{info.contactItem.name}}
                </DropdownItem>
                <DropdownItem v-if="info.contactItem.email">
                  <a :href="'mailto:' + info.contactItem.email"><Icon type="email"></Icon> Email</a>
                </DropdownItem>
                <DropdownItem v-if="info.contactItem.url">
                  <a :href="info.contactItem.url"><Icon type="ios-home-outline"></Icon> 主页</a>
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </DropdownMenu>
        </Dropdown>
      </div>

      <el-container class="info-body">
        <el-main class="info-desc">
          <div v-if="info.description" v-html="info.description"></div>
          <div v-else>暂无文档描述。</div>
        </el-main>
        <el-footer></el-footer>
      </el-container>

    </el-card>
  </div>
</template>

<script>
import commonUtils from '@/util/common_utils'
export default {
  name: 'info',
  data () {
    return {
      info: {}
    }
  },
  mounted () {
    var info = this.$store.state.api.infoItem
    commonUtils.handleMarkdown(info, 'description')

    this.info = info
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .box-card{
    min-height: 600px;
  }
  .info-title {
    font-size: 20px;
  }
  .info-desc {
    overflow-y: auto;
  }
  .about-btn {
    float: right;
    padding: 3px 0;
  }
</style>



