import jsYaml from 'js-yaml'
import Vue from 'vue'

var urlResolver = {
  resolve (url, success, failed) {
    if (!url) {
      throw new Error('Url required')
    }

    Vue.resource(url).get().then(({data}) => {
      if (url.slice(-5) === '.yaml') {
        console.log('it is yaml doc, convert to json...')
        var then = new Date().getTime()
        data = jsYaml.load(data)
        var now = new Date().getTime()
        console.log('yaml to json toke ' + (now - then) + ' ms.')
      }

      success && success(data)
    }, resp => {
      failed && failed(resp)
    })
  }
}

export default urlResolver



