<template>
  <div class="url" @keyup.enter="analyse">

    <div class="big-title">Swagger+</div>

    <el-input placeholder="请输入 swagger.json 的链接" v-model="url" class="url-input" clearable :autofocus="true" ref="urlInput">
      <el-button slot="append" icon="el-icon-success" @click="analyse">解析</el-button>
    </el-input>

    <el-card v-show="historyLength > 0" class="url-history">
      <div slot="header" class="clearfix">
        <span>历史记录</span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="emptyHistory">清空</el-button>
      </div>
      <div v-show="showHistory">
        <history ref="history" @lengthChange="lengthChange" @urlSelected="urlSelected"></history>
      </div>
    </el-card>

  </div>
</template>

<script>
  import history from '@/components/history'
  import apiResolver from '@/service/api_resolver'
  export default {
    props: [
      'targetUrl'
    ],
    components: {
      history
    },
    data () {
      return {
        url: '',
        historyLength: 0,
        showHistory: true
      }
    },
    mounted () {
      if (this.targetUrl) {
        this.url = this.targetUrl
        this.analyse()
        return
      }
      this.showHistory = true
    },
    methods: {
      analyse () {
        if (!this.url || this.url.trim() === '') {
          this.$message.error('请输入URL！')
          return
        }

        // history
        this.$refs.history.addHistory(this.url)

        // resolve
        apiResolver.resolve(this, this.url)
      },
      urlSelected (url) {
        this.url = url
        this.$refs.urlInput.focus()
      },
      lengthChange (length) {
        this.historyLength = length
      },
      emptyHistory () {
        this.$refs.history.emptyHistory()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .big-title {
    font-size: 60px;
    font-weight: bold;
    font-family: Helvetica;
    color: rgb(84, 92, 100);
  }
  .url {
    margin-top: 100px;
    text-align: center;
    width: 100%;
  }
  .url-input, .url-history, .big-title {
    width: 80%;
    margin: 5px auto;
    text-align: center;
  }
</style>



