import markdown from 'marked'

var commonUtils = {
  openUrl (url) {
    var otherWindow = window.open()
    otherWindow.opener = null
    otherWindow.location = url
  },
  handleMarkdown (obj, mdProp) {
    if (!obj || !obj[mdProp]) {
      return null
    }

    var parsed = obj[mdProp + 'Parsed']
    if (!parsed) {
      var result = markdown(obj[mdProp])
      obj[mdProp] = '<div class="markdown-content">' + result + '</div>'

      obj[mdProp + 'Parsed'] = true
    }
  }
}

export default commonUtils



