<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  import Api from '@/model/Api'
  export default {
    name: 'app',
    mounted () {
      var api = new Api()
      api.info = 'info'
    }
  }
</script>

<style lang="sass">
  #app
    font-family: 'Avenir', Helvetica, Arial, sans-serif
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    text-align: center
    color: #2c3e50
    height: 100%
  body
    margin: 0px
    padding: 0px
    height: 100%
    font-size: 14px
  html
    margin: 0px auto
    padding: 0px
    height: 100%
  li, div
    text-align: left
  .display-block
    display: block
  .router-link-active li
    color: rgb(255, 208, 75) !important
    background-color: rgb(67, 74, 80) !important
  .CodeMirror
    height: 600px !important
  .el-table .success-row
    background: #f0f9eb
  .content-deprecated input, span.content-deprecated
    text-decoration: line-through
  .markdown-content
    ol, ul
      list-style-type: disc
      padding-left: 40px
      margin: 16px 0px
</style>



