<template>
  <div>

    <el-tabs v-model="activeName">
      <el-tab-pane label="通用设置" name="common">
        <el-form ref="form" label-width="160px">
          <el-form-item label="当前文档URL">
            <el-input placeholder="请输入内容" v-model="url" class="input-with-select" readonly>
              <!--<el-button slot="append" icon="el-icon-search"></el-button>-->
            </el-input>
            <el-tooltip class="item" effect="dark" content="将此URL记录至浏览器本地数据库，方便常用API文档的访问" placement="bottom">
              <el-checkbox v-model="isRememberUrl">
                记住它
                <i class="el-icon-warning"></i>
              </el-checkbox>
            </el-tooltip>
          </el-form-item>
          <el-form-item v-if="rememberedUrl && !isRememberUrl" label="已记住URL">
            <el-input placeholder="请输入内容" v-model="rememberedUrl" class="input-with-select" readonly>
            </el-input>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="界面显示" name="layout">
        <el-form ref="form" label-width="160px">
          <el-form-item label="启用导航">
            <el-switch v-model="showNav"></el-switch>
          </el-form-item>
          <el-form-item label="导航显示接口描述">
            <el-switch v-model="showNavDesc"></el-switch>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="历史记录" name="history">
        <el-button class="clean-btn" type="primary" size="mini" icon="el-icon-delete" @click="emptyHistory">清空</el-button>
        <history ref="history" @urlSelected="urlSelected"></history>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  import store from 'store'
  import history from '@/components/history'
  var innerTrigger = false
  export default {
    props: [
      'showSetting'
    ],
    data () {
      return {
        activeName: 'common',
        isRememberUrl: null,
        rememberedUrl: null
      }
    },
    mounted () {
      this.reload()
    },
    watch: {
      showSetting (show) {
        if (show) {
          this.reload()
        }
      },
      isRememberUrl (checked) {
        if (innerTrigger) return
        if (checked) {
          store.set('rememberUrl', this.url)
          this.rememberedUrl = this.url
        } else if (this.url === this.rememberedUrl) {
          store.set('rememberUrl', null)
          this.rememberedUrl = null
        }
      }
    },
    computed: {
      showNav: {
        get () {
          return this.$store.state.showNav
        },
        set (value) {
          this.$store.commit('showNav', value)
        }
      },
      showNavDesc: {
        get () {
          return this.$store.state.showNavDesc
        },
        set (value) {
          this.$store.commit('showNavDesc', value)
        }
      },
      url: {
        get () {
          return this.$store.state.url
        }
      }
    },
    methods: {
      reload () {
        innerTrigger = true
        this.isRememberUrl = false
        var rememberUrl = store.get('rememberUrl')
        if (rememberUrl && rememberUrl === this.url) {
          this.isRememberUrl = true
        }
        this.rememberedUrl = rememberUrl
        innerTrigger = false
      },
      urlSelected (url) {
        this.$router.push({name: 'url', params: {targetUrl: url}})
      },
      emptyHistory () {
        this.$refs.history.emptyHistory()
      }
    },
    components: {
      history
    }
  }
</script>

<style scoped>
  .clean-btn {
    margin-bottom: 10px;
  }
</style>



