// common used api model in swagger+
import GroupItem from '@/model/nav/GroupItem'

var Api = class Api {
  host          // String
  basePath      // String
  schemes       // String[]

  infoItem      // InfoItem: information of this api
  pathItems     // PathItem[]: all paths in this api
  modelItems    // ModelItem[]: all models in this api
  tagItems      // TagItem[]: all tags in this api
  _tags         // Object: tag names object for unique tag name checking

  nav = null
  getNav () {
    // return cache
    if (this.nav) return this.nav

    // convert
    var groupItems = []
    for (var tagItem of this.tagItems) {
      var groupItem = new GroupItem()
      groupItem.name = tagItem.name
      groupItem.description = tagItem.description
      groupItem.pathItems = findPathItemsByTagName.call(this, groupItem.name)
      groupItems.push(groupItem)
    }
    removeGroupItemsWithNoPathItems(groupItems)

    this.nav = groupItems
    return this.nav
  }
}

function findPathItemsByTagName (tagName) {
  var pathItems = []
  for (var pathItem of this.pathItems) {
    if (pathItem.tags && pathItem.tags.indexOf(tagName) >= 0) {
      pathItems.push(pathItem)
    }
  }
  return pathItems
}

function removeGroupItemsWithNoPathItems (groupItems) {
  for (var i = groupItems.length - 1; i >= 0; i--) {
    var groupItem = groupItems[i]
    if (groupItem.pathItems.length === 0) {
      groupItems.splice(i, 1)
    }
  }
}

export default Api



