var apiResolver = {
  resolve (vue, url) {
    vue.$store.commit('initApi', {
      url,
      success: () => {
        vue.$router.push({name: 'info'})
        vue.$store.commit('showNav', true)
      },
      failed: ({status, statusText}) => {
        vue.$notify.error({
          title: '错误状态码：' + (status || 404),
          message: statusText || 'Not Found'
        })
      }
    })
    vue.$store.commit('setUrl', url)
  }
}

export default apiResolver



