import Vuex from 'vuex'
import apiConvertor from '@/service/api_convertor'
import urlResolver from '@/service/url_resolver'

const store = new Vuex.Store({
  state: {
    url: null,
    api: null,
    showNav: true, // whether show navigator bar
    showNavDesc: true
  },
  mutations: {
    initApi (state, {url, success, failed}) {
      // resolve
      urlResolver.resolve(url, doc => {
        var api = apiConvertor.convert(doc)
        state.api = api

        success && success()
      }, ({status, statusText}) => {
        failed && failed({status, statusText})
      })
    },
    setUrl (state, url) {
      state.url = url
    },
    showNav (state, bool) {
      state.showNav = bool
    },
    showNavDesc (state, bool) {
      state.showNavDesc = bool
    }
  }
})

export default store



