<template>
  <div>
    <div class="line"></div>
    <el-menu
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      :router="false"
      @select="handleSelect"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >

        <router-link :to="{name: 'home'}">
          <el-menu-item index="homepage">
            <span class="app-title">Swagger+</span>
          </el-menu-item>
        </router-link>

      <el-popover ref="setting" placement="bottom-end" width="600" trigger="click" @show="showSetting = true" @hide="showSetting = false">
        <setting v-if="showSetting" :showSetting="showSetting"></setting>
      </el-popover>

      <el-tooltip class="item" effect="dark" content="设置" placement="bottom">
        <el-menu-item index="1" v-popover:setting>
          <i class="el-icon-setting"></i>
        </el-menu-item>
      </el-tooltip>

      <!--<el-tooltip class="item" effect="dark" content="帮助" placement="bottom">-->
        <!--<el-menu-item index="2">-->
          <!--<i class="el-icon-question"></i>-->
        <!--</el-menu-item>-->
      <!--</el-tooltip>-->

      <el-tooltip class="item" effect="dark" content="关于" placement="right">
        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-info"></i>
          </template>
          <el-menu-item index="2-1">功能特性</el-menu-item>
          <el-menu-item index="2-2">更新日志</el-menu-item>
        </el-submenu>
      </el-tooltip>


    </el-menu>

  </div>
</template>

<script>
  import setting from '@/components/setting'
  export default {
    data () {
      return {
        activeIndex: '0',
        showSetting: false
      }
    },
    methods: {
      handleSelect (key, keyPath) {
      }
    },
    components: {
      setting
    }
  }
</script>

<style scoped>
  .app-title {
    font-size: 24px;
    font-weight: bold;
    font-family: Helvetica;
  }
</style>



