<template>
  <div class="layout">
    <el-container class="outter-container">
      <transition name="fade">
        <el-aside v-if="nav" class="left-nav">
          <left-nav></left-nav>
        </el-aside>
      </transition>

      <el-main class="inner-container">
        <router-view/>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  import leftNav from '@/components/left_nav'
  import { mapState } from 'vuex'
  export default {
    data () {
      return {
      }
    },
    mounted () {
    },
    computed: mapState({
      nav: 'showNav'
    }),
    components: {
      leftNav
    }
  }
</script>

<style scoped>
  .layout, .inner-container {
    width: 100%;
  }

   .layout, .left-nav, .inner-container, .outter-container {
    height: 100%;
  }

  .left-nav {
    width: 300px;
  }

</style>



