<template>
  <div class="info">
    <div class="api-title">
      <div class="api-sum" :class="'api-method-' + p.method.toUpperCase()" v-if="p.summary">
        <i class="el-icon-info"></i>
        <span class="api-sum-text">{{p.summary}}</span>
      </div>
      <el-tooltip class="item" effect="dark" content="已被标记为'已过时'，应减少使用" placement="bottom-start" :disabled="!p.deprecated">
        <el-input placeholder="请输入内容" v-model="p.path" class="input-with-select" :class="p.deprecated ? 'content-deprecated':''" readonly>
          <span class="api-method" :class="'api-method-' + p.method.toUpperCase()" slot="prepend">{{p.method.toUpperCase()}}</span>

          <el-tooltip slot="append" class="item" effect="dark" content="复制完整请求路径" placement="left">
            <el-button type="primary" class="copy-btn" icon="ivu-icon ivu-icon-ios-copy-outline" @click="copyPath(p.path)"></el-button>
          </el-tooltip>
        </el-input>
      </el-tooltip>
    </div>
    <el-tabs class="api-tab" :value="'first'" @tab-click="changeTab">
      <el-tab-pane label="接口信息" name="first" style="padding: 4px">

        <el-card v-if="p.description" class="box-card">
          <div slot="header" class="clearfix card-header">
            <span>接口描述</span>
            <!--<el-button style="float: right; padding: 3px 0" type="text">操作按钮</el-button>-->
          </div>
          <div>
            <span v-html="p.description"></span>
          </div>
        </el-card>
        <el-card v-else class="box-card">
          <span>暂无接口描述</span>
        </el-card>

        <el-card v-if="parameters && parameters.length > 0" class="box-card">
          <div slot="header" class="clearfix card-header">
            <span>参数信息</span>
            <!--<el-button style="float: right; padding: 3px 0" type="text">操作按钮</el-button>-->
          </div>
          <div>
            <el-table :data="parameters" style="width: 100%" :row-class-name="handleRowClassName" :border="true"
                      :default-sort = "{prop: 'required', order: 'descending'}">
              <el-table-column prop="name" label="参数名称" width="180" fixed="left" sortable>
                <template slot-scope="scope">
                  <span v-if="!scope.row.deprecated">{{scope.row.name}}</span>
                  <span v-else>
                    <el-tooltip slot="append" class="item" effect="dark" content="已被标记为'已过时'，应减少使用" placement="right">
                      <span>
                        <span class="content-deprecated">{{scope.row.name}}</span>
                        <i class="el-icon-warning"></i>
                      </span>
                    </el-tooltip>
                  </span>
                </template>
              </el-table-column>
              <el-table-column prop="dataType" label="数据类型" width="120" sortable/>
              <el-table-column prop="required" label="必填" width="120" sortable>
                <template slot-scope="scope">
                  <i v-if="scope.row.required" class="el-icon-success"></i>
                  <i v-else class="el-icon-error"></i>
                </template>
              </el-table-column>
              <el-table-column prop="in" label="请求位置" width="120" sortable/>
              <el-table-column prop="description" label="说明">
                <template slot-scope="scope">
                  <div v-html="scope.row.description"></div>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-card>
        <el-card v-else class="box-card">
          <span>暂无参数信息</span>
        </el-card>

        <el-card v-if="pathItem.responseItems && pathItem.responseItems.length > 0" class="box-card">
          <div slot="header" class="clearfix card-header">
            <span>返回信息</span>
            <!--<el-button style="float: right; padding: 3px 0" type="text">操作按钮</el-button>-->
          </div>
          <div>
            <el-table :data="pathItem.responseItems" style="width: 100%" :row-class-name="handleRowClassName" :border="true"
                      :default-sort = "{prop: 'code', order: 'ascending'}">
              <el-table-column prop="code" label="状态编码" width="180" fixed="left" sortable/>
              <el-table-column prop="dataType" label="数据类型" width="120" sortable/>
              <el-table-column prop="description" label="说明">
                <template slot-scope="scope">
                  <div v-html="scope.row.description"></div>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-card>
        <el-card v-else class="box-card">
          <span>暂无返回信息</span>
        </el-card>

      </el-tab-pane>
      <el-tab-pane label="调试测试" name="third">敬请期待。</el-tab-pane>
      <el-tab-pane label="原生源码" name="raw">
        <el-card v-if="showRaw" class="code-card" :body-style="{ padding: '0px' }">
          <codemirror v-model="p.raw" :options="cmOptions"></codemirror>
        </el-card>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import commonUtils from '@/util/common_utils'
// require component
import { codemirror } from 'vue-codemirror'

// require styles
import 'codemirror/lib/codemirror.css'
// theme css
import 'codemirror/theme/duotone-light.css'
// language js
import 'codemirror/mode/javascript/javascript.js'

export default {
  name: 'info',
  props: {
    method: String,
    path: String,
    pathItem: {
      type: Object,
      default () {
        return {method: ''}
      }
    }
  },
  data () {
    return {
      info: {},
      showRaw: false,
      cmOptions: {
        tabSize: 2,
        mode: 'text/javascript',
        theme: 'duotone-light',
        lineNumbers: true,
        line: true,
        readOnly: true
      },
      p: {method: 'GET', parameterItems: []},     // short for pathItem
      parameters: []
    }
  },
  mounted () {
    this.reload(this.pathItem)
  },
  watch: {
    pathItem: function (newValue) {
      this.reload(newValue)
    }
  },
  methods: {
    reload (newPathItem) {
      // pathItem
      var founded = false
      if (!newPathItem || !newPathItem.path) {
        for (let pathItem of this.$store.state.api.pathItems) {
          if (pathItem.method === this.method && pathItem.path === this.path) {
            this.p = pathItem
            founded = true
            break
          }
        }
      } else {
        this.p = newPathItem
        founded = true
      }
      if (!founded) {
        this.$router.push({name: 'url'})
        return
      }
      commonUtils.handleMarkdown(this.p, 'description')

      // parameterItems
      this.parameters = this.p.parameterItems.map(param => {
        if (!param.description) {
          param.description = '无'
        }
        commonUtils.handleMarkdown(param, 'description')
        return param
      })
    },
    changeTab (tab, event) {
      if (tab.name === 'raw') {
        this.showRaw = true
      } else {
        this.showRaw = false
      }
    },
    handleRowClassName ({row, rowIndex}) {
      if (row.required) {
        return 'success-row'
      }

      return ''
    },
    copyPath (path) {
      var $api = this.$store.state.api
      var fullPath = $api.schemes[0] + '://' + $api.host + $api.basePath + path
      this.$clipboard(fullPath)
      this.$message('已将完整请求路径复制到剪切板。')
    }
  },
  components: {
    codemirror
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .api-sum {
    font-size: 18px;
    font-weight: bold;
    margin: 5px;
    color: #409EFF;
  }
  .api-sum-text {
    /*border-bottom: 2px solid #409EFF;*/
  }
  .api-title .input-with-select {
    font-size: 18px;
    font-weight: bold;
    font-family: Helvetica, Arial, sans-serif;
  }
  .api-tab {
    margin-top: 10px;
  }
  .box-card {
    margin-bottom: 10px;
  }
  .card-header {
    font-size: 14px;
    height: 20px;
  }
  .no-content {
    color: #7a7f8a;
  }
  .copy-btn {
    font-size: 18px;
  }
  .el-icon-success {
    color: green;
  }
  .el-icon-error {
    color: darkgrey;
  }
  .api-method-POST {
    color: #67c23a;
  }
  .api-method-GET {
    color: #409EFF;
  }
  .api-method-DELETE {
    color: #fa5555;
  }
  .api-method-PUT {
    color: #eb9e05;
  }
  .api-method-PATCH {
    color: #878d99;
  }
  .code-card {
    margin: 10px;
  }
</style>



