<template>
  <div class="left-nav">

    <el-menu class="el-menu-vertical-demo" :collapse="isCollapse" :router="false"
             background-color="#545c64"
             text-color="#fff"
             active-text-color="#ffd04b"
             ref="navBar">

      <router-link :to="{name: 'info'}">
        <el-menu-item index="info">
          <i class="el-icon-info"></i>
          <span slot="title">文档描述</span>
        </el-menu-item>
      </router-link>

      <el-submenu index="iApis">
        <template slot="title">
          <div class="sub-menu">
            <i class="el-icon-upload"></i>
            <span slot="title">接口列表</span>
          </div>
        </template>

        <el-menu-item-group v-for="groupItem in nav" :key="groupItem.name" class="tag-group">
          <el-tooltip slot="title" class="item tag-title" effect="dark" placement="right" :open-delay="800" :disabled="!groupItem.description">
            <div class="tooptip-box" slot="content">{{groupItem.description}}</div>
            <span class="display-block">
              <span class="tag-name">{{groupItem.name}}</span>
              <span class="tag-desc" v-if="groupItem.description"> | {{groupItem.description}}</span>
            </span>
          </el-tooltip>

          <el-tooltip v-for="pathItem in groupItem.pathItems" :key="pathItem.method + pathItem.path" class="item" effect="dark"
                       placement="right" :open-delay="800" :disabled="!pathItem.summary">
            <div class="tooptip-box" slot="content">{{pathItem.summary}}</div>

            <router-link :to="{name: 'api', params: {method: pathItem.method, path: pathItem.path, pathItem}}">
              <el-menu-item :index="pathItem.method + pathItem.path" class="api-item">
                <el-tag class="api-method" :type="resolveTagType(pathItem.method)" size="mini">{{pathItem.method.toUpperCase()}}</el-tag>

                <el-tooltip class="item" effect="dark" placement="top" :open-delay="800">
                  <div class="tooptip-box" slot="content">{{pathItem.path}}</div>
                  <el-tag class="api-path" :class="pathItem.deprecated ? 'content-deprecated':''" size="mini" type="info">{{pathItem.path}}</el-tag>
                </el-tooltip>

                <br/>
                <span v-if="showNavDesc" class="api-desc">{{pathItem.summary}}</span>
              </el-menu-item>
            </router-link>

          </el-tooltip>

        </el-menu-item-group>
      </el-submenu>

      <el-submenu index="iModels">
        <template slot="title">
          <div class="sub-menu">
            <i class="el-icon-upload"></i>
            <span slot="title">模型列表</span>
          </div>
        </template>
        <div v-for="modelItem in $store.state.api.modelItems" :key="modelItem.name">
          <el-tooltip class="model-item" effect="dark" placement="right" :open-delay="800" :disabled="!modelItem.description">
            <div class="tooptip-box" slot="content">{{modelItem.description}}</div>

            <router-link :to="{name: 'model', params: {name: modelItem.name, modelItem}}">
              <el-menu-item :index="'model_' + modelItem.name" class="">
                <i class="el-icon-star-on"></i>
                {{modelItem.name}}
                <br/>
                <span class="">{{modelItem.description}}</span>
              </el-menu-item>
            </router-link>

          </el-tooltip>
        </div>

      </el-submenu>

    </el-menu>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    props: [],
    data () {
      return {
        isCollapse: false,
        nav: null
      }
    },
    mounted () {
      this.nav = this.$store.state.api.getNav()
      this.openApis()
    },
    computed: mapState({
      showNavDesc: 'showNavDesc'
    }),
    methods: {
      resolveTagType (method) {
        switch (method) {
          case 'get':
          case 'GET':
            return null
          case 'post':
          case 'POST':
            return 'success'
          case 'delete':
          case 'DELETE':
            return 'danger'
          case 'patch':
          case 'PATCH':
            return 'info'
          case 'put':
          case 'PUT':
            return 'warning'
        }
        return null
      },
      openApis () {
        this.$refs.navBar.open('iApis')
      }
    }
  }
</script>

<style scoped>
  .left-nav {
    height: 100%;
  }
  .tag-name {
    font-size: 18px;
    font-weight: bold;
    color: #FFF;
  }
  .tag-title {
    margin-top: 0px;
  }
  .tag-desc, .api-desc {font-size: 12px;
    color: lightgrey;
  }
  .api-item {
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 25px;
    border-top: 1px solid #99999940;
    padding: 5px;
    height: auto;
    min-height: 40px;
  }
  .api-method {
    width: 54px;
    display: inline-block;
    text-align: center;
  }
  .api-path {
    color: #ffffff;
    font-size: 14px;
  }
  .tooptip-box {
    max-width: 300px;
  }
  .tag-group {
    border-bottom: 1px solid #b4bccc;
  }
  .el-menu-vertical-demo {
    overflow-x: hidden;
    height: 100%;
  }
  .is-active .sub-menu {
    color: rgb(255, 208, 75) !important;
  }
</style>



