var ParameterItem = class ParameterItem {
  name                  // String: required

  dataType              // String: requied

  in                    // String: required, possible values are "query", "header", "path" or "cookie".

  required = false      // Boolean

  description           // String

  deprecated = false    // Boolean
}

export default ParameterItem



